import React, { PureComponent, Children } from "react";
import PropTypes from "prop-types";
import {
  SelectTrigger as StyledSelectTrigger,
  INPUT_MIN_WIDTH
} from "../styled/StyledStateless";
import SelectInput from "./SelectInput";
import ReactDOM from "react-dom";

export default class SelectTrigger extends PureComponent {
  static defaultProps = {
    onOpenChange: () => {},
    onEnterPressed: () => {},
    onChange: () => {},
    isOpen: false,
    isInputHidden: false,
    triggerIcon: null,
    children: null,
    placeholder: null,
    value: ""
  };

  static propTypes = {
    onOpenChange: PropTypes.func,
    onChange: PropTypes.func,
    onEnterPressed: PropTypes.func,
    isOpen: PropTypes.bool,
    triggerIcon: PropTypes.element,
    children: PropTypes.oneOfType([
      PropTypes.arrayOf(PropTypes.element),
      PropTypes.element
    ]),
    value: PropTypes.string,
    placeholder: PropTypes.string,
    isInputHidden: PropTypes.bool
  };

  constructor() {
    super();
    this.state = {};
  }

  componentDidMount() {
    this.updateInputWidth();
  }

  componentDidUpdate(prevProps) {
    if (
      (!prevProps.isOpen && this.props.isOpen) ||
      Children.count(prevProps.children) !== Children.count(this.props.children)
    ) {
      this.updateInputWidth();
    }
  }

  onClick = () => {
    this.props.onOpenChange({
      isOpen: !this.props.isOpen
    });
  };

  handleTriggerRef = ref => {
    this.trigger = ref;
  };

  handleInputRef = ref => {
    this.selectInput = ref;
  };

  getInput() {
    if (this.props.isInputHidden) {
      return null;
    }

    return (
      <SelectInput
        onEnterPressed={this.props.onEnterPressed}
        inputWidth={this.state.inputWidth}
        ref={this.handleInputRef}
        placeholder={this.props.placeholder}
        isOpen={this.props.isOpen}
        onChange={this.props.onChange}
        value={this.props.value}
      />
    );
  }

  updateInputWidth() {
    const triggerNode = ReactDOM.findDOMNode(this.trigger);
    const selectInputNode = ReactDOM.findDOMNode(this.selectInput);
    const computedStyle = getComputedStyle(triggerNode);
    const padding =
      parseFloat(computedStyle.paddingLeft) +
      parseFloat(computedStyle.paddingRight);
    const containerWidth = triggerNode.clientWidth - padding;
    let widthOfItemsInLastLine = 0;
    [...triggerNode.childNodes].forEach(childNode => {
      if (childNode === selectInputNode) {
        return;
      }
      const { offsetWidth } = childNode;
      if (
        widthOfItemsInLastLine < containerWidth &&
        widthOfItemsInLastLine + offsetWidth > containerWidth
      ) {
        widthOfItemsInLastLine = 0;
      }
      widthOfItemsInLastLine += offsetWidth + 1;
    });
    const freeWidthInLine = containerWidth - widthOfItemsInLastLine;
    if (freeWidthInLine > INPUT_MIN_WIDTH) {
      this.setState({
        inputWidth: freeWidthInLine
      });
    } else {
      this.setState({
        inputWidth: containerWidth
      });
    }
  }

  render() {
    return (
      <StyledSelectTrigger onClick={this.onClick} ref={this.handleTriggerRef}>
        {this.props.triggerIcon}
        {this.props.children}
        {this.getInput()}
      </StyledSelectTrigger>
    );
  }
}



