import React, { PureComponent } from "react";
import PropTypes from "prop-types";
import {
  DroplistItemWrapper,
  DroplistItemAvatar,
  DroplistItemName,
  DroplistItemDescription
} from "../styled/StyledDroplist";
import { UserPropsType } from "../../types/UserPropsType";

export default class DroplistItem extends PureComponent {
  static defaultProps = {
    item: {},
    onClick: () => {},
    withAvatar: false
  };

  static propTypes = {
    withAvatar: PropTypes.bool,
    onClick: PropTypes.func,
    item: UserPropsType
  };

  constructor(props) {
    super(props);
    this.state = {
      isLoaded: false
    };
    this.image = new Image();
    this.image.addEventListener("load", this.onImageLoaded);
    this.image.src = this.props.item.avatarUrl;
  }

  componentWillUnmount() {
    this.image.removeEventListener("load", this.onImageLoaded);
  }

  onImageLoaded = () => {
    this.setState({
      isLoaded: true
    });
  }

  onClick = evt => {
    evt.preventDefault();
    this.props.onClick(this.props.item);
  };

  getAvatar() {
    if (!this.props.withAvatar) {
      return null;
    }
    return <DroplistItemAvatar>{this.getImage()}</DroplistItemAvatar>;
  }

  getImage() {
    if (!this.state.isLoaded) {
      return null;
    }
    return <img src={this.props.item.avatarUrl} alt={this.props.item.name} />
  }

  render() {
    return (
      <DroplistItemWrapper onClick={this.onClick}>
        {this.getAvatar()}
        <DroplistItemName>{this.props.item.name}</DroplistItemName>
        <DroplistItemDescription>
          {this.props.item.description}
        </DroplistItemDescription>
      </DroplistItemWrapper>
    );
  }
}



