import React, { PureComponent } from "react";
import PropTypes from "prop-types";
import ReactDOM from "react-dom";
import Droplist from "../../droplist";
import SelectTrigger from "./SelectTrigger";
import {
  SelectWrapper,
  StatusInfoMessage,
  DroplistContainer,
  AddIcon,
  ArrowIcon,
  MultiSelectPlaceholderButton,
  MultiSelectItemWrapper
} from "../styled/StyledStateless";
import SelectedItems from "./SelectedItems";
import { UsersPropsType } from "../../types/UserPropsType";
import strings from "../../strings";

export default class Stateless extends PureComponent {
  static propTypes = {
    isMulti: PropTypes.bool,
    selectedItems: UsersPropsType,
    items: UsersPropsType,
    onOpenChange: PropTypes.func,
    onRemove: PropTypes.func,
    onChange: PropTypes.func,
    onSelect: PropTypes.func,
    placeholder: PropTypes.string,
    value: PropTypes.string,
    isOpen: PropTypes.bool,
    isFiltered: PropTypes.bool,
    isLoading: PropTypes.bool,
    droplistVisibleItemsLimit: PropTypes.number,
    itemHeight: PropTypes.number,
    withAvatar: PropTypes.bool,
    noMatchesFound: PropTypes.string,
    loadingMessage: PropTypes.string,
    multiPlaceholder: PropTypes.string,
    multiAllItemsSelected: PropTypes.string
  };

  static defaultProps = {
    isMulti: false,
    selectedItems: [],
    items: [],
    onOpenChange: () => {},
    onChange: () => {},
    onRemove: () => {},
    onSelect: () => {},
    value: "",
    isFiltered: false,
    isOpen: false,
    isLoading: false,
    droplistVisibleItemsLimit: 5,
    itemHeight: 50,
    withAvatar: false,
    ...strings
  };

  componentDidMount() {
    document.addEventListener("click", this.handleClickOutside, true);
    document.addEventListener("keydown", this.handleEsc);
  }

  componentWillUnmount() {
    document.removeEventListener("click", this.handleClickOutside, true);
    document.removeEventListener("keydown", this.handleEsc);
  }

  handleClickOutside = event => {
    if (this.props.isOpen) {
      const domNode = ReactDOM.findDOMNode(this);

      if (
        !domNode ||
        (event.target instanceof Node && !domNode.contains(event.target))
      ) {
        this.props.onOpenChange({ isOpen: false });
      }
    }
  };

  onEnterPressed = () => {
    if (this.props.items.length) {
      this.props.onSelect(this.props.items[0]);
    }
  };

  handleEsc = event => {
    if (event.keyCode === 27) {
      this.props.onOpenChange({ isOpen: false });
    }
  };

  getStatusInfoMessage() {
    if (this.props.isLoading) {
      return this.props.loadingMessage;
    }

    if (
      this.props.isFiltered &&
      this.props.items.length === 0 &&
      this.props.value
    ) {
      return this.props.noMatchesFound;
    }

    if (this.props.selectedItems.length > 0 && this.props.items.length === 0) {
      return this.props.multiAllItemsSelected;
    }
  }

  getListContent() {
    const statusInfoMessage = this.getStatusInfoMessage();

    if (statusInfoMessage) {
      return <StatusInfoMessage>{statusInfoMessage}</StatusInfoMessage>;
    }

    return (
      <Droplist
        withAvatar={this.props.withAvatar}
        isOpen={this.props.isOpen}
        items={this.props.items}
        onItemClick={this.props.onSelect}
      />
    );
  }

  getDroplistHeight() {
    if (this.props.items.length >= this.props.droplistVisibleItemsLimit) {
      return this.props.droplistVisibleItemsLimit * this.props.itemHeight;
    }
  }

  getDroplist() {
    if (this.props.isOpen) {
      return (
        <DroplistContainer height={this.getDroplistHeight()}>
          {this.getListContent()}
        </DroplistContainer>
      );
    }
  }

  getMultiSelectPlaceholderButton() {
    if (!this.isMultiSelectPlaceholderButtonVisible()) {
      return null;
    }

    return (
      <MultiSelectItemWrapper>
        <MultiSelectPlaceholderButton>
          {this.props.multiPlaceholder}
          <AddIcon />
        </MultiSelectPlaceholderButton>
      </MultiSelectItemWrapper>
    );
  }

  getSelectedItems() {
    if (!this.props.isMulti) {
      return null;
    }

    return (
      <SelectedItems
        items={this.props.selectedItems}
        onRemove={this.props.onRemove}
      />
    );
  }

  isMultiSelectPlaceholderButtonVisible() {
    return (
      this.props.isMulti &&
      !this.props.isOpen &&
      this.props.selectedItems.length > 0
    );
  }

  render() {
    return (
      <SelectWrapper>
        <SelectTrigger
          isInputHidden={this.isMultiSelectPlaceholderButtonVisible()}
          ref={this.handleTriggerRef}
          placeholder={this.props.placeholder}
          value={this.props.value}
          isOpen={this.props.isOpen}
          onChange={this.props.onChange}
          onOpenChange={this.props.onOpenChange}
          onEnterPressed={this.onEnterPressed}
          triggerIcon={<ArrowIcon />}
        >
          {this.getSelectedItems()}
          {this.getMultiSelectPlaceholderButton()}
        </SelectTrigger>
        {this.getDroplist()}
      </SelectWrapper>
    );
  }
}



