import React from "react";
import Select from "./select";
import styled from "styled-components";
import Droplist from "./droplist";
import items from "./data";
import { withEmailAsFilter, jitterTime, cleanString } from "./util/helpers";
import blindFilterStrategy from "./util/blindFilterStrategy";

const AppWrapper = styled.div`
  margin: 0 auto;
  display: flex;
  flex-wrap: wrap;
  width: 800px;
  justify-content: space-between;
`;
const Preview = styled.div`
  flex: ${props => `0 0 ${props.width || 45}%`};
`;

const Header = styled.h3``;

function shouldSearchRemotely(filter) {
  const cleanedFilter = cleanString(filter);
  return cleanedFilter.length > 1 && cleanedFilter.indexOf("@") !== -1;
}

function remoteSearch(filter) {
  // тоже самое, что и запрос на http://example.com/search?filter=${filter}
  console.log("remoteSearch: ", filter);
  return new Promise(resolve => {
    setTimeout(() => {
      const response = withEmailAsFilter(blindFilterStrategy)(filter, items);
      console.log("remoteSearch response: ", filter, response);
      resolve(response);
    }, jitterTime(1000, 0.5));
  });
}

function onSelect(item) {
  console.log("select: ", item);
}

const defaultProps = {
  items,
  filterStrategy: blindFilterStrategy,
  remoteSearch,
  placeholder: "Введите имя друга или email",
  noMatchesFound: "Пользователь не найден",
  loadingMessage: "Идет поиск по email",
  multiPlaceholder: "Добавить",
  multiAllItemsSelected: "Все пользователи были добавлены",
  onSelect
};

export default function App() {
  return (
    <AppWrapper>
      <Preview>
        <Header>Single select without avatar</Header>
        <Select {...defaultProps} />
      </Preview>
      <Preview>
        <Header>Single select with avatar</Header>
        <Select {...defaultProps} withAvatar />
      </Preview>
      <Preview>
        <Header>Multi select without avatar</Header>
        <Select {...defaultProps} isMulti />
      </Preview>
      <Preview>
        <Header>Multi select with avatar</Header>
        <Select {...defaultProps} isMulti withAvatar />
      </Preview>
      <Preview>
        <Header>Single select with remote search</Header>
        <Select
          {...defaultProps}
          withAvatar
          shouldSearchRemotely={shouldSearchRemotely}
        />
      </Preview>
      <Preview>
        <Header>Multi select with remote search</Header>
        <Select
          {...defaultProps}
          isMulti
          withAvatar
          shouldSearchRemotely={shouldSearchRemotely}
        />
      </Preview>
      <Preview>
        <Header>Multi select with preselected items</Header>
        <Select
          {...defaultProps}
          withAvatar
          isMulti
          shouldSearchRemotely={shouldSearchRemotely}
          selectedItems={items.slice(0,3)}
        />
      </Preview>
      <Preview>
        <Header>Single select with preselected item</Header>
        <Select
          {...defaultProps}
          withAvatar
          value={items[0].name}
        />
      </Preview>
      <Preview>
        <Header>Without scroll</Header>
        <Select
          {...defaultProps}
          isMulti
          withAvatar
          items={items.slice(0, 4)}
        />
      </Preview>
      <Preview width="30">
        <Header>Smaller size</Header>
        <Select
          {...defaultProps}
          isMulti
          withAvatar
          shouldSearchRemotely={shouldSearchRemotely}
        />
      </Preview>
      <Preview width="60">
        <Header>Bigger size</Header>
        <Select
          {...defaultProps}
          isMulti
          withAvatar
          shouldSearchRemotely={shouldSearchRemotely}
        />
      </Preview>
      <Preview width="100">
        <Header>Used data</Header>
        <pre>{JSON.stringify(items, null, 2)}</pre>
      </Preview>
    </AppWrapper>
  );
}



