export const defaultGetValue = item => item.name;

export function cleanString(str) {
  return str
    .trim()
    .toLowerCase()
    .replace(/[\(\)\.,\-]+/g, "");
}

export function withSpaceAtStart(str) {
  return ` ${str}`;
}


export function jitterTime(interval = 60 * 1000, jitter = 0.1) {
  const max = interval * (1 + jitter);
  const min = interval * (1 - jitter);
  return Math.floor(Math.random() * (max - min)) + min;
}

export function withEmailAsFilter(filterStrategy) {
  return (filter, items) => {
    const updatedFilter = filter.replace("@", " ");
    return filterStrategy(updatedFilter, items, item =>
      item.email.replace("@", " ")
    );
  };
}


