import React, { PureComponent } from "react";
import PropTypes from "prop-types";
import {
  SelectedItem as StyledSelectedItem,
  DeleteIcon,
  MultiSelectItemWrapper
} from "../styled/StyledStateless";
import { UserPropsType } from "../../types/UserPropsType";

export default class SelectedItem extends PureComponent {
  static defaultProps = {
    item: {},
    onRemove: () => {}
  };

  static propTypes = {
    onRemove: PropTypes.func,
    item: UserPropsType
  };

  onWrapperClick = e => {
    e.stopPropagation();
  };

  onIconClick = e => {
    e.stopPropagation();
    e.preventDefault();
    this.props.onRemove(this.props.item.id);
  };

  render() {
    return (
      <MultiSelectItemWrapper onClick={this.onWrapperClick}>
        <StyledSelectedItem>
          {this.props.item.name}
          <DeleteIcon onClick={this.onIconClick} />
        </StyledSelectedItem>
      </MultiSelectItemWrapper>
    );
  }
}



