import styled, { css } from "styled-components";
import { truncate } from "../../util/styled/mixins";
import { ArrowIcon, DeleteIcon, AddIcon } from "./Icons";

export * from "./Icons";
export const INPUT_MIN_WIDTH = 120;

export const SelectWrapper = styled.div`
  position: relative;
  display: block;
`;

export const SelectTrigger = styled.div`
  background: #fff;
  border: 1px solid #d3d9de;
  border-radius: 2px;
  padding: 2px 2px 1px;
  position: relative;
  overflow: visible;
  font-size: 12.5px;
  & > ${ArrowIcon} {
    float: right;
  }
  &:after {
    content: "";
    display: table;
    clear: both;
  }
`;

export const Input = styled.input.attrs({ type: "text" })`
  float: left;
  outline: none;
  border: none;
  margin: 0;
  height: 25px;
  font-size: 13px;
  padding: 4px 6px 7px;
  box-sizing: border-box;
  width: ${props =>
    INPUT_MIN_WIDTH > props.inputWidth ? INPUT_MIN_WIDTH : props.inputWidth}px;
`;

export const SelectedItem = styled.div`
  display: inline-block;
  position: relative;
  font-size: 0.9em;
  vertical-align: middle;
  background-color: #5181b8;
  border-radius: 2px;
  padding: 5px 22px 5px 7px;
  line-height: 12px;
  max-width: 200px;
  color: #fff;
  ${truncate()};
  & ${DeleteIcon} {
    position: absolute;
    right: 5px;
    top: 50%;
    transform: translateY(-50%);
  }
`;

export const MultiSelectItemWrapper = styled.div`
  padding: 0px 5px 3px 0px;
  float: left;
`;

export const MultiSelectPlaceholderButton = styled.div`
  background: #dae2ea;
  position: relative;
  border-radius: 2px;
  padding: 4px 22px 3px 8px;
  line-height: 15px;
  text-align: left;
  max-width: 110px;
  box-sizing: border-box;
  color: #55677d;
  cursor: pointer;
  ${truncate()}
  &  ${AddIcon} {
    position: absolute;
    right: 5px;
    top: 50%;
    transform: translateY(-50%);
  }
`;

export const SelectInputWrapper = styled.div`
  float: left;
`;

export const StatusInfoMessage = styled.div`
  padding: 18px 15px 17px;
  background: #fafbfc;
  font-size: 13px;
`;

export const DroplistContainer = styled.div`
  z-index: 1;
  background: #fff;
  border: solid #c9d0d6;
  border-width: 0 1px 1px;
  height: ${props => props.height}px;
  position: absolute;
  left: 0;
  right: 0;
  & .custom-scroll .inner-handle {
    background-color: #dae2ea;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
  }
`;



