import styled from "styled-components";
import { truncate } from "../../util/styled/mixins";

const AvatarSize = 34;

export const DroplistItemAvatar = styled.div`
  float: left;
  width: ${AvatarSize}px;
  height: ${AvatarSize}px;
  margin-right: 10px;
  & img {
    width: 100%;
    height: 100%;
    border-radius: 50%;
    display: block;
  }
`;

export const DroplistItemName = styled.div`
  text-align: left;
  ${truncate()};
`;

export const DroplistItemDescription = DroplistItemName.extend`
  padding-top: 4px;
  line-height: 14px;
`;

export const DroplistItemWrapper = styled.div`
  width: 100%;
  border-top: 1px solid #e7e8ec;
  cursor: pointer;
  font-size: 0.9em;
  padding: 8px;
  min-height: 50px;
  box-sizing: border-box;
  &:hover {
    background-color: #f0f2f5;
  }
  &:first-child {
    border-top: none;
  }
`;


