import langHelpers from "./langHelpers";
import { defaultGetValue, cleanString, withSpaceAtStart } from './helpers';

export default function blindFilterStrategy(
  filter,
  items,
  getTextFromItem = defaultGetValue
) {
  const cleanedFilter = cleanString(filter);
  const filterValues = [withSpaceAtStart(cleanedFilter)];
  const ruToEn = langHelpers.fromRuToEnKeyboardLayout(cleanedFilter);
  if (ruToEn) {
    filterValues.push(withSpaceAtStart(ruToEn));
  }

  const enToRu = langHelpers.fromEnToRuKeyboardLayout(cleanedFilter);
  if (enToRu) {
    filterValues.push(withSpaceAtStart(enToRu));
  }

  const itemsWithText = items.map(item => {
    const value = cleanString(getTextFromItem(item));
    const words = value.split(" ");
    let newValue = withSpaceAtStart(value);
    for (const word of words) {
      const cyrylToLatin = langHelpers.fromCyrylToLatin(word);
      if (cyrylToLatin) {
        newValue += withSpaceAtStart(cyrylToLatin);
      }
      const latinToCyryl = langHelpers.fromLatinToCyryl(word);
      if (latinToCyryl) {
        newValue += withSpaceAtStart(latinToCyryl);
      }
    }
    return newValue;
  });

  const filteredItems = [];

  for (const index in itemsWithText) {
    for (const filterValue of filterValues) {
      if (itemsWithText[index].indexOf(filterValue) !== -1) {
        filteredItems.push(items[index]);
        break;
      }
    }
  }

  return filteredItems;
}


