import styled from "styled-components";
import { retinaImage } from "polished";

export const ArrowIcon = styled.span`
  ${retinaImage("arrowIcon", "11px 7px")};
  background-position: 50% 50%;
  background-repeat: no-repeat;
  width: 25px;
  height: 25px;
  cursor: pointer;
`;

const commonSprite = styled.span`
  ${retinaImage("common", "32px 232px")};
  background-position: 0 0;
  background-repeat: no-repeat;
  transition: opacity 0.4s;
  opacity: 0.45;
  &:hover {
    opacity: 1;
  }
`

export const DeleteIcon = commonSprite.extend`
  background-position: 1px -160px;
  width: 12px;
  height: 12px;
  cursor: pointer;
`;

export const AddIcon = commonSprite.extend`
  width: 12px;
  height: 12px;
  cursor: pointer;
`;



