import React, { PureComponent } from "react";
import PropTypes from "prop-types";
import debounce from "debounce";
import Stateless from "./Stateless";
import { UsersPropsType } from "../../types/UserPropsType";
import strings from "../../strings";

const DEFAULT_DEBOUNCE_WAIT_INTERVAL = 200;

export default class Statefull extends PureComponent {
  static propTypes = {
    filterStrategy: PropTypes.func,
    shouldSearchRemotely: PropTypes.func,
    remoteSearch: PropTypes.func,
    onSelect: PropTypes.func,
    isMulti: PropTypes.bool,
    noMatchesFound: PropTypes.string,
    loadingMessage: PropTypes.string,
    multiPlaceholder: PropTypes.string,
    multiAllItemsSelected: PropTypes.string,
    placeholder: PropTypes.string,
    withAvatar: PropTypes.bool,
    selectedItems: UsersPropsType,
    items: UsersPropsType,
    debounceWaitInterval: PropTypes.number,
    value: PropTypes.string
  };

  static defaultProps = {
    filterStrategy: (filter, items) => items,
    shouldSearchRemotely: () => {},
    remoteSearch: () => Promise.resolve([]),
    onSelect: () => {},
    isMulti: false,
    items: [],
    selectedItems: [],
    withAvatar: false,
    value: "",
    debounceWaitInterval: DEFAULT_DEBOUNCE_WAIT_INTERVAL,
    ...strings
  };

  constructor(props) {
    super(props);
    this.state = {
      isOpen: false,
      filter: props.value,
      filteredItems: [],
      isFiltered: false,
      selectedItems: props.selectedItems,
      isLoading: false
    };
    this.onFilterWithDebounce = debounce(
      this.onFilter,
      this.props.debounceWaitInterval
    );
  }

  componentDidMount(){
    if (this.state.filter) {
      this.onFilterWithDebounce(this.state.filter);
    }
  }

  componentDidUpdate(prevProps, prevState) {
    if (!this.state.isOpen && prevState.isOpen) {
      this.onFilterWithDebounce.clear();
    }
  }

  componentWillUnmount() {
    this.onFilterWithDebounce.clear();
  }

  onChange = filter => {
    this.setState({
      isFiltered: false,
      isLoading: false,
      filter
    });
    this.onFilterWithDebounce(filter);
  };

  onFilter(filter) {
    if (this.props.shouldSearchRemotely(filter)) {
      if (!this.props.remoteSearch) {
        console.warn(
          "You should define remoteSearch property to search remotely"
        );
        return;
      }

      this.setState({
        isLoading: true,
        filteredItems: []
      });

      this.props
        .remoteSearch(filter)
        .then(filteredItems => {
          if (filter === this.state.filter) {
            this.setState({
              isLoading: false,
              isFiltered: true,
              filteredItems
            });
          }
        })
        .catch(() => {
          if (filter === this.state.filter) {
            this.setState({
              isLoading: false,
              isFiltered: true
            });
          }
        });
      return;
    }

    this.setState({
      isFiltered: true,
      filteredItems: this.filterItems(filter)
    });
  }

  onOpenChange = ({ isOpen }) => {
    this.setState({
      isOpen
    });
  };

  onDroplistItemClick = item => {
    if (this.props.isMulti) {
      this.setState(state => ({
        isOpen: false,
        filter: "",
        selectedItems: state.selectedItems.concat(item)
      }));
      return;
    }
    this.setState({
      isOpen: false,
      filter: item.name,
      filteredItems: this.filterItems(item.name)
    });
    this.props.onSelect(item);
  };

  onRemove = id => {
    const selectedItems = this.state.selectedItems.filter(i => i.id !== id);
    this.setState({
      selectedItems,
      isOpen: false
    });
  };

  getItems() {
    const items = this.state.filter
      ? this.state.filteredItems
      : this.props.items;
    const selectedIds = this.state.selectedItems.map(item => item.id);
    return items.filter(item => selectedIds.indexOf(item.id) === -1);
  }

  filterItems(filter) {
    return this.props.filterStrategy(filter, this.props.items);
  }

  render() {
    return (
      <Stateless
        onSelect={this.onDroplistItemClick}
        onRemove={this.onRemove}
        selectedItems={this.state.selectedItems}
        items={this.getItems()}
        isFiltered={this.state.isFiltered}
        isLoading={this.state.isLoading}
        value={this.state.filter}
        isOpen={this.state.isOpen}
        onOpenChange={this.onOpenChange}
        onChange={this.onChange}
        isMulti={this.props.isMulti}
        noMatchesFound={this.props.noMatchesFound}
        loadingMessage={this.props.loadingMessage}
        multiPlaceholder={this.props.multiPlaceholder}
        multiAllItemsSelected={this.props.multiAllItemsSelected}
        placeholder={this.props.placeholder}
        withAvatar={this.props.withAvatar}
      />
    );
  }
}



