import React, { PureComponent, Fragment } from "react";
import PropTypes from "prop-types";
import SelectedItem from "./SelectedItem";
import { UsersPropsType } from "../../types/UserPropsType";

export default class SelectedItems extends PureComponent {
  static defaultProps = {
    items: [],
    onRemove: () => {}
  };

  static propTypes = {
    items: UsersPropsType,
    onRemove: PropTypes.func
  };

  getSelectedList() {
    return this.props.items.map(item => (
      <SelectedItem key={item.id} item={item} onRemove={this.props.onRemove} />
    ));
  }

  render() {
    return <Fragment>{this.getSelectedList()}</Fragment>;
  }
}



